.grid {
    margin: 40px 0 0;
}

.heading {
    margin-top: 25px;
    font-family: cursive;
    font-size: 20px;
}

.rest {
  margin-right: 7.5px;
}

.Btn {
    margin-top: 10px;
    margin-left: 10px;
  --b: 3px;   /* border thickness */
  --s: .45em; /* size of the corner */
  --color: #373B44;
  
  padding: calc(.5em + var(--s)) calc(.9em + var(--s));
  color: var(--color);
  --_p: var(--s);
  background:
    conic-gradient(from 90deg at var(--b) var(--b),#0000 90deg,var(--color) 0)
    var(--_p) var(--_p)/calc(100% - var(--b) - 2*var(--_p)) calc(100% - var(--b) - 2*var(--_p));
  transition: .3s linear, color 0s, background-color 0s;
  outline: var(--b) solid #0000;
  outline-offset: .6em;
  font-size: 16px;

  border: 0;

  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.Btn:hover,
.Btn:focus-visible{
  --_p: 0px;
  outline-color: var(--color);
  outline-offset: .05em;
}

.Btn:active {
  background: var(--color);
  color: #fff;
}

.Btn00 {
  margin-right: 100px;
}

.Btn01 {
  margin-left: 125px;
}

.Btn0 {
  margin-left: 10px;
  background-color: transparent;
  border: 1px solid #266DB6;
  box-sizing: border-box;
  color: #00132C;
  font-family: "Avenir Next LT W01 Bold",sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  padding: 16px 23px;
  position: relative;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.Btn0:hover,
.Btn0:active {
  outline: 0;
}

.Btn0:hover {
  background-color: transparent;
  cursor: pointer;
}

.Btn0:before {
  background-color: #D5EDF6;
  content: "";
  height: calc(100% + 3px);
  position: absolute;
  right: -7px;
  top: -9px;
  transition: background-color 300ms ease-in;
  width: 100%;
  z-index: -1;
}

.Btn0:hover:before {
  background-color: #6DCFF6;
}

@media (min-width: 768px) {
  .Btn0 {
    padding: 16px 32px;
  }
}