  50% {
    transform: scale(1.2);
    background-color: rgb(255, 254, 106);
  }

  100% {
    transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
} */

@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: #32CD32;
  }

  50% {
    transform: scale(1.2);
    background-color: #32CD32;
  }

  100% {
    transform: scale(1);
    background-color: #32CD32;
  }
}